 -->

<template>
<div class="content">

  <!-- <div>/</div> -->
    <div>
      <a-row>
        <a-col :span="6">
          {{dataSources2.semesterName}}
        </a-col>
        <a-col :span="5">
          {{dataSources2.studentName}}
        </a-col>
          {{dataSources2.stuNo}}
      </a-row>
      <br>
      <a-row>
        <a-col :span="6">
          {{dataSources2.departmentName}}
        </a-col>
        <a-col :span="5">
          {{dataSources2.specialtyName}}
        </a-col>
          {{dataSources2.studentClass}}
      </a-row>
    </div>
    <br>
    <!--  -->
      <template> 
        <a-form :form="searchForm" @submit="searchSubmit" layout="inline" >
          <a-form-item label="">
            <a-select v-decorator="['semesterName']" placeholder="" optionFilterProp="children" showSearch
              :filterOption="filterSelectedOption" style="width: 200px"  @select="select" :defaultActiveFirstOption="false"> 
              <a-select-option v-for="item1,index in semesterList" :key="index" :value="item1">{{item1}}</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item label="">
            <a-select v-decorator="['courseName']" placeholder="" optionFilterProp="children" showSearch
              :filterOption="filterSelectedOption" style="width: 200px" @dropdownVisibleChange="openselect" :defaultActiveFirstOption="false">
              <a-select-option v-for="item2,index in courseList" :key="index" :value="item2">{{item2}}</a-select-option>
            </a-select>
          </a-form-item>

          <a-form-item class="form-btn-item"> <!--  -->
            <a-button class="filter-btn" html-type="submit" type="primary" icon="search"></a-button>
            <a-button class="reset-btn" type="danger" icon="reload" @click="reset"></a-button>
          </a-form-item>
        </a-form>
      </template> 
    <!--  -->
    <template>
      <a-table 
          :columns="Columns2"
          :dataSource="dataSources2.data"
          :pagination="pagination2"
          :loading="load"
          bordered
          size="small"
          :scroll="{ x: 800, y: 340 }"
          :row-key="record => record.id"
          @change="onChange"
        >
        <a-button slot="courseNumber" slot-scope="_,record" style="border-style: none" @click="showcourse(record.courseNumber)">{{record.courseNumber}}</a-button>
        <a-button slot="courseName" slot-scope="_,record" style="border-style: none" @click="showcourse(record.courseNumber)">{{record.courseName}}</a-button>
        <a-button slot="operation1" slot-scope="_,record" type="primary" @click="showDetailed(record)"></a-button>
      </a-table>
    </template>
    <!-- 1 -->
    <a-modal title="" :width="650" :visible="visible0" @ok="handleOk0" :confirmLoading="confirmLoading0"
      @cancel="handleCancel0">
      <template>
        {{this.coursedata.number}}<br>
        {{this.coursedata.courseName}}<br>
        {{this.coursedata.courseEname}}<br>
        {{this.coursedata.speciesName}}<br>
        {{this.coursedata.categoryName}}<br>
        {{this.coursedata.classificationName}}<br>
        {{this.coursedata.exam_typeName}}<br>
        {{this.coursedata.exam_modeName}}<br>
        {{this.coursedata.hour}}<br>
        {{this.coursedata.credit}}
      </template>
    </a-modal>

</div>
</template>

<script>

  // _1 
  const Columns2 = [
    {
      title: '',
      key: 'courseNumber',//ok
      width: 110,
      scopedSlots: {
        customRender: 'courseNumber'
      },
    }, {
      title: '',
      dataIndex: 'courseName',
      scopedSlots: {
        customRender: 'courseName'
      },
    }, {
      title: '',
      dataIndex: 'speciesName',//1111111111111
      width: 120,
    }, {
      title: '',
      dataIndex: 'categoryName',//1111111111111  categoryName
      width: 110,
    }, {
      title: '',
      dataIndex: 'score',//
      width: 100,
      sorter: (a, b) => a.score - b.score,
    }, {
      title: '',
      dataIndex: 'point',//ok
      width: 80,
      sorter: (a, b) => a.point - b.point,
    }, {
      title: '',
      dataIndex: 'state',//ok
      width: 90,
    }, {
      title: '',
      dataIndex: 'teacherName',
      width: 100,
    }, {
      fixed: 'right',
      title: '',
      key: 'operation1',//ok
      width: 100,
      scopedSlots: {
      customRender: 'operation1'
      },
    }
  ];

export default {
  data(){
    return{
      Columns2,
      load:false,

      searchForm:this.$form.createForm(this),
      semesterList: [
          "2014-2015-1",
          "2014-2015-2",
          "2015-2016-1",
      ],//
      courseList: [
          "",
          "",
          "",
      ],//
      ifsemester:0,//

      visible0:false,
      confirmLoading0:false,
      coursedata:{},//

      dataSources2:[],//
      pagination2:{total:0,pageSize:5},
      
      inputId:{
        courseId:'',
        stuGradeMainId:'',
        studentId:'',
        universityId:'',
        semesterId:'',
        stuItemGradeDetailOldId:''
      },//id
      data0:"1",
      
  //
      Detaileddata:[],//
      data: [
          {
          score: undefined,
          rate: undefined,
          name: '',
          count: undefined,
          id: 1
          },{
          score: undefined,
          rate: undefined,
          name: '',
          count: undefined,
          id: 2
          },{
          score: undefined,
          rate: undefined,
          name: '',
          count: '',
          id: 3
          },{
          score: undefined,
          rate: undefined,
          name: '',
          count: undefined,
          id: 4
          },{
          score: undefined,
          rate: undefined,
          name: '',
          count: undefined,
          id: 5
          },{
          score: undefined,
          rate: undefined,
          name: '',
          count: undefined,
          id: 6
          },
      ], //_1
      inputId:{
        courseId:'',
        stuGradeMainId:'',
        studentId:'',
        universityId:'',
        semesterId:'',
        stuItemGradeDetailOldId:''
      }
    }

  },
      created: function () {
        this.axios
          .get("/json/gradeManagement1/selectcurrentStudent",)
          .then(response => {
            // this.load=false,
            this.dataSources2=response.data.data;
            this.pagination2={total:response.data.data.total,pageSize:5}
            if(this.dataSources2.data.state==0)
              this.dataSources2.data.state=""
            else if(this.dataSources2.data.state==1)
              this.dataSources2.data.state=""
            else if(this.dataSources2.data.state==2)
              this.dataSources2.data.state=""
            console.log(this.dataSources2);
          })
          .catch(function (error) {
            console.log(error);
        });
        this.axios
          .get("/json/gradeManagement1/getsemesterNameAll",)
          .then(response=>{
            this.semesterList=response.data.data;
              console.log(response.data.data);
          })
          .catch(function (error) {
              console.log(error);
        });

      },
  methods: {
    //
      filterSelectedOption(input, option) { //
        return option.componentOptions.children[0].text.toLowerCase().indexOf(input.toLowerCase()) >= 0
      },
      btn: function () {
        var search = this.search;
        if (search) {
          this.searchData = this.products.filter(function (product) {
            console.log(product)
            return Object.keys(product).some(function (key) {
              console.log(key)
              return String(product[key]).toLowerCase().indexOf(search) > -1
            })
          })
        }
      },//
      openselect(){
        console.log("");
        if(this.ifsemester==0){
          this.axios
          .get("/json/gradeManagement1/getcourseNameAll",)
          .then(response=>{
            this.courseList=response.data.data;
            console.log(this.courseList);
          })
          .catch(function (error) {
              console.log(error);
          })
        }
      },//
      select(value){
        this.axios
        .get("/json/gradeManagement1/getcourseNameAll",
            {
              params: {
                semesterName: value
              }
            }
          )
        .then(response=>{
          this.courseList=response.data.data;
          this.ifsemester=1,//semester
          console.log(value);
        })
        .catch(function (error) {
            console.log(error);
        }),
          this.searchForm.setFieldsValue({courseName:""})
      },//
      reset(){
        this.searchForm.resetFields()
        this.ifsemester=0
      },//
      searchSubmit (e) {
        e.preventDefault();
        console.log('ok');
        this.searchForm.validateFields((err, values) => {
          if (!err) {
            console.log(values);
            this.axios
            .get("/json/gradeManagement1/selectcurrentStudent",//GET /json/gradeManagement1/selectcurrentStudent
                {
                  params: {
                    semesterName: values.semesterName,
                    courseName:values.courseName
                  }
                }
              )
            .then(response=>{
              this.dataSources2=response.data.data;
              this.fetch(this.pagination2.current,1);
              if(dataSources2.data.state==0)
                dataSources2.data.state=""
              else if(dataSources2.data.state==1)
                dataSources2.data.state=""
              else if(dataSources2.data.state==2)
                dataSources2.data.state=""
              console.log(this.dataSources2);
            })
            .catch(function (error) {
                console.log(error);
            })
          }
        });
      },//

    //     
      showcourse(value){
        this.axios
        .get("/json/gradeManagement1/selectBycourseNumber",
            {
              params: {
                courseNumber:value
              }
            }
          )
        .then(response=>{
          this.coursedata=response.data.data;
          console.log(this.coursedata);
        })
        .catch(function (error) {
            console.log(error);
        })
        this.visible0=true
      },//
      handleOk0(e) {
        this.confirmLoading0 = true;
        setTimeout(() => { this.visible0 = false; this.confirmLoading0 = false; }, 500);
      }, //model0
      handleCancel0(e) {
        this.visible0 = false;
      }, //model

    //      
      showDetailed(value) { //
        this.axios
          .get("/json/gradeManagement1/selectCourseItem", {
            params: {
              id: value.id,
            }
          })
          .then(response => {
             for(let i=0;i<6;i++){
               if( response.data.data[i]!=undefined)
                 {
                    this.data[i] = response.data.data[i];
                    if(this.data[i].name==1)
                    this.data[i].name = '';
                    else if(this.data[i].name==2)
                    this.data[i].name = '';
                    else if(this.data[i].name==3)
                    this.data[i].name = '';
                    else if(this.data[i].name==4)
                    this.data[i].name = '';
                    else if(this.data[i].name==5)
                    this.data[i].name = '';
                    else if(this.data[i].name==6)
                    this.data[i].name = '';
                 }
             };
            this.Detaileddata=this.data;
            this.inputId.courseId=value.courseId
            this.inputId.studentId=value.studentId
            this.inputId.universityId=value.universityId
            this.inputId.semesterId=value.semesterId
            this.inputId.stuGradeMainId=value.id
            console.log(this.inputId);
            console.log('this.value  S');
            this.$router.push({
              path:"./DetailedSearchGrade",
              query:{
                data1:this.Detaileddata,
                data2:this.dataSources2,
                data3:this.inputId,
                data0:this.data0
              }
            });
          })
          .catch(function (error) {
            console.log(error);
          });
      }, //model1 
      
    //
      onChange(pagination, sorter) {
        this.fetch(pagination.current,12);
      },//
      //
      fetch(current,pageSize){
        this.pagination2={total:this.dataSources2.total,pageSize:5}
      },
  }
}
</script>

<style>

</style>


