<!--
 * @Description: /
 * @Author: Lin Changkun
 * @LastEditors: Lin Changkun
 * @Date: 2019-04-29 15:22:17
 * @LastEditTime: 2019-06-19 11:45:07
 -->

<template>
  <div class="content">
    <!--  -->
    <a-table
      :columns="columns"
      :dataSource="data"
      :pagination="pagination"
      :loading="load"
      @change="handleChange"
      :row-key="record => record.applyId"
      :scroll="{}"
      class="my-tooltip"
    >
      <!--  -->
      <span slot="tooltip" slot-scope="text">
        <a-tooltip placement="top">
          <template slot="title">{{text}}</template>
          <span>{{text}}</span>
        </a-tooltip>
      </span>

      <!--  -->
      <div slot="operation" slot-scope="text,record" type="primary">
        <a-row :gutter="8">
          <a-col :span="12">
            <a-button type="primary" ghost @click="detailDrawer(record)"></a-button>
          </a-col>
          <a-col :span="12">
            <a-button type="primary" ghost @click="showDrawer(record)"></a-button>
          </a-col>
        </a-row>
      </div>
    </a-table>

    <!--  -->
    <a-drawer title="" width="520" :closable="false" @close="onClose" :visible="visible">
      <!--  -->
      <a-form :form="modelform" layout="vertical" hideRequiredMark @submit="handleSubmit">
        <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
          <a-textarea
            v-decorator="[
            'reply', 
            {
              rules: [{ required: true, message: '' }]
            }]"
            :rows="4"
            placeholder=""
          />
        </a-form-item>
        <br>
        <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
          <!--  -->
          <a-radio-group
            @change="isAgree"
            v-decorator="[
            'value', 
            {
              rules: [{ required: true, message: '' }]
            }]"
          >
            <a-radio :value="1"></a-radio>
            <a-radio :value="2"></a-radio>
          </a-radio-group>
          <br>
          <br>
        </a-form-item>
        <a-button type="primary" html-type="submit"></a-button>
      </a-form>
    </a-drawer>

    <!--  -->
    <a-drawer
      title=""
      :width="520"
      @close="detailOnClose"
      :visible="detailVisible"
      :wrapStyle="{height: 'calc(100% - 108px)',overflow: 'auto',paddingBottom: '10 8px'}"
    >
      <a-form :form="modelform" layout="vertical" hideRequiredMark>
        <a-row :gutter="16">
          <a-col :span="12">
            <a-form-item label="" id="newScore">
              <p>{{newScore}}</p>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="" id="oldScore">
              <p>{{oldScore}}</p>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="12">
            <a-form-item label="" id="newRegName">
              <p>{{newRegName}}</p>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="" id="oldRegName">
              <p>{{oldRegName}}</p>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="12">
            <a-form-item label="" id="newRegDepartment">
              <p>{{newRegDepartment}}</p>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="" id="oldRegDepartment">
              <p>{{oldRegDepartment}}</p>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="12">
            <a-form-item label="" id="newRegTime">
              <p>{{newRegTime}}</p>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="" id="oldRegTime">
              <p>{{oldRegTime}}</p>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="12">
            <a-form-item label="" id="newAudName">
              <p>{{newAudName}}</p>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="" id="oldAudName">
              <p>{{oldAudName}}</p>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="12">
            <a-form-item label="" id="newAudDepartment">
              <p>{{newAudDepartment}}</p>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="" id="oldAudDepartment">
              <p>{{oldAudDepartment}}</p>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="12">
            <a-form-item label="" id="newAudTime">
              <p>{{newAudTime}}</p>
            </a-form-item>
          </a-col>
          <a-col :span="12">
            <a-form-item label="" id="oldAudTime ">
              <p>{{oldAudTime}}</p>
            </a-form-item>
          </a-col>
        </a-row>
        <br>
        <a :href="aa">
          <a-button type="primary" icon="download"></a-button>
        </a>
      </a-form>
    </a-drawer>
    <br><br><br><br><br>
  </div>
</template>

<script>
const columns = [
  // {
  //   title: "",
  //   dataIndex: "applyId",
  //   key: "applyId",
  //   width: 130,
  //   onFilter: (value, record) => record.applyId.indexOf(value) === 0,
  //   sorter: (a, b) => a.applyId - b.applyId //
  // },
  {
    title: "",
    dataIndex: "course",
    key: "course",
    width: 150,
    scopedSlots: { customRender: "tooltip" },
    onFilter: (value, record) => record.course.indexOf(value) === 0,
    sorter: (a, b) => a.course.localeCompare(b) //ASCII
  },
  {
    title: "",
    dataIndex: "courseItem",
    key: "courseItem",
    width: 130,
    onFilter: (value, record) => record.courseItem.indexOf(value) === 0,
    sorter: (a, b) => a.courseItem.localeCompare(b) //ASCII
  },
  {
    title: "",
    dataIndex: "studentName",
    key: "studentName",
    width: 120,
    scopedSlots: { customRender: "tooltip" },
    onFilter: (value, record) => record.studentName.indexOf(value) === 0,
    sorter: (a, b) => a.studentName.localeCompare(b)
  },
  {
    title: "",
    dataIndex: "studentNumber",
    key: "studentNumber",
    width: 140,
    onFilter: (value, record) => record.studentNumber.indexOf(value) === 0,
    sorter: (a, b) => a.studentNumber.localeCompare(b)
  },
  {
    title: "",
    dataIndex: "studentClass",
    key: "studentClass",
    width: 150,
    scopedSlots: { customRender: "tooltip" },
    onFilter: (value, record) => record.studentClass.indexOf(value) === 0,
    sorter: (a, b) => a.studentClass.localeCompare(b)
  },
  {
    title: "",
    dataIndex: "initiationTime",
    key: "initiationTime",
    width: 130,
    onFilter: (value, record) => record.initiationTime.indexOf(value) === 0,
    sorter: (a, b) => a.initiationTime.localeCompare(b)
  },
  {
    title: "",
    dataIndex: "reason",
    key: "reason",
    width: 150,
    scopedSlots: { customRender: "tooltip" },
    onFilter: (value, record) => record.reason.indexOf(value) === 0,
    sorter: (a, b) => a.reason.localeCompare(b)
  },
  {
    title: "",
    dataIndex: "originatorName",
    key: "originatorName",
    width: 120,
    onFilter: (value, record) => record.originatorName.indexOf(value) === 0,
    sorter: (a, b) => a.originatorName.localeCompare(b)
  },
  // {
  //   title: "",
  //   dataIndex: "auditorName",
  //   key: "auditorName",
  //   width: 120,
  //   onFilter: (value, record) => record.auditorName.indexOf(value) === 0,
  //   sorter: (a, b) => a.auditorName.localeCompare(b)
  // },
  // {
  //   title: "",
  //   dataIndex: "auditorDepartment",
  //   key: "auditorDepartment",
  //   onFilter: (value, record) => record.auditorDepartment.indexOf(value) === 0,
  //   sorter: (a, b) => a.auditorDepartment.localeCompare(b)
  // },
  // {
  //   title: "",
  //   dataIndex: "reply",
  //   key: "reply",
  //   width: 150,
  //   scopedSlots: { customRender: "tooltip" },
  //   onFilter: (value, record) => record.reply.indexOf(value) === 0,
  //   sorter: (a, b) => a.reply.localeCompare(b)
  // },
  {
    title: "",
    dataIndex: "status",
    key: "status",
    width: 150,
    onFilter: (value, record) => record.status.indexOf(value) === 0,
    sorter: (a, b) => a.status.localeCompare(b)
  },
  {
    title: "",
    dataIndex: "operation",
    key: "operation",
    width: 230,
    // fixed: "right",
    // slots: { name: 'operation'},
    scopedSlots: { customRender: "operation" }
  }
];

const data = [
  {
    key: " ",
    applyId: " ",
    course: " ",
    courseItem: " ",
    studentName: " ",
    studentNumber: " ",
    studentClass: " ",
    initiationTime: " ",
    reason: " ",
    originatorName: " ",
    auditorName: " ",
    auditorDepartment: " ",
    reply: " ",
    status: " "
  }
];

export default {
  data() {
    return {
      modelform: this.$form.createForm(this),
      visible: false,
      detailVisible: false,
      data,
      columns,
      pagination: {
        pageSize: 5 // 
      },
      load: false,
      tempapplyId: undefined,
      aa: "www.changkunhaoshuai.com",

      newScore: "",
      newRegName: "",
      newRegDepartment: "",
      newRegTime: "",
      newAudName: "",
      newAudDepartment: "",
      newAudTime: "",
      oldScore: "",
      oldRegName: "",
      oldRegDepartment: "",
      oldRegTime: "",
      oldAudName: "",
      oldAudDepartment: "",
      oldAudTime: ""
    };
  },

  methods: {
    // onChange,
    callback(key) {
      console.log(key);
    },

    /**
     * get
     * @page: 
     * @limit: 
     */
    fetch(pageNum, limit) {
      this.load = true;
      this.axios
        .get("/json/gradeManagement1/mycheckAud/", {
          params: {
            pageNum: pageNum
          }
        })
        .then(response => {
          this.load = false;
          let res = response.data;
          console.log(res);
          // 
          this.data = res.data.list;
          // this.formData = res.data.list;
          // console.log(formData);
          // 
          this.pagination = {
            total: res.data.total,
            pageSize: res.data.pageSize
          };
        });
    },

    /**
     * 
     */
    handleChange(pagination) {
      console.log(pagination);
      // 
      this.fetch(pagination.current, pagination.pageSize);
    },

    /**
     * 
     *  ajax
     */
    handleSubmit(e) {
      e.preventDefault();
      // 
      this.modelform.validateFields((err, values) => {
        // console.log(values);
        if (err) {
          // 
          console.log(": ", values);
        } else {
          let load = this.$message.loading("", 0);
          this.axios
            .post("/json/gradeManagement1/dwabyAud/", {
              //this.url1
              applyId: this.tempapplyId,
              status: values.value,
              reply: values.reply
            })
            .then(res => {
              // 
              load();
              if (res.data.code === 0) {
                // 
                this.$message.success("", 4);
                // 
                this.modelform = this.$form.createForm(this);
                this.visible = false;
              } else {
                this.$message.error(res.data.msg, 4);
                this.visible = false;
              }
            });
        }
      });
    },

    /**  */
    isAgree(e) {
      // this.tempvalue =  e.target.value;
      console.log("radio checked", e.target.value);
    },

    /** */
    showDrawer(record) {
      console.log(record);
      this.tempapplyId = record.applyId;
      this.visible = true;
    },
    onClose() {
      this.visible = false;
    },
    detailOnClose() {
      this.detailVisible = false;
    },

    /**
     *
     */
    detailDrawer(record) {
      this.newScore = record.newScore;
      this.newRegName = record.newRegName;
      this.newRegDepartment = record.newRegDepartment;
      this.newRegTime = record.newRegTime;
      this.newAudName = record.newAudName;
      this.newAudDepartment = record.newAudDepartment;
      this.newAudTime = record.newAudTime;
      this.oldScore = record.oldScore;
      this.oldRegName = record.oldRegName;
      this.oldRegDepartment = record.oldRegDepartment;
      this.oldRegTime = record.oldRegTime;
      this.oldAudName = record.oldAudName;
      this.oldAudDepartment = record.oldAudDepartment;
      this.oldAudTime = record.oldAudTime;
      this.aa = "/json/gradeManagement1/download?path=" + record.attachment;
      this.detailVisible = true;
    }
  },

  /**
   * 
   * ,
   */
  mounted() {
    this.fetch(1, this.pagination.pageSize);
  }
};
</script>

<style>
/* .content {
  background: #fff;
  padding: 30px 30px;
} */
</style>


