<!--
 * @Description: ///
 * @Author: Lin Changkun
 * @LastEditors: Lin Changkun
 * @Date: 2019-04-29 15:22:09
 * @LastEditTime: 2019-06-20 22:35:54
 -->
 <template>
  <div class="content">
    <a-form :form="modelform" layout="horizontal" hideRequiredMark @submit="handleSubmit">
      <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 10 }">
        <!--  treeData  JSON  -->
        <a-tree-select
          v-decorator="[
              'itemDetail',
              {
                rules: [{ required: true, message: '' }]
              }]"
          style="width: 250px"
          :dropdownStyle="{ maxHeight: '400px', overflow: 'auto' }"
          :treeData="treeData"
          placeholder=""
          treeDefaultExpandAll
          @select="inputSelect"
        >
          <!-- <span style="color: #08c" slot="title" slot-scope="{key, value}" v-if="key='1'">{{value}}</span> -->
        </a-tree-select>
      </a-form-item>

      <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 7 }">
        <a-textarea
          v-decorator="[
              'content',
              {
                rules: [{ required: true, message: '' }]
              }]"
          :rows="4"
          placeholder=""
        />
      </a-form-item>

      <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 10 }">
        <a-input-number
          v-decorator="[
              'score',
              {
                rules: [{ required: true, message: '' }]
              }]"
          style="width: 150px"
          :min="0"
          :max="100"
          :step="1"
          :precision="1.0"
        />
      </a-form-item>

      <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 7 }">
        <a-textarea
          v-decorator="[
              'note',
              {
                rules: [{ required: true, message: '' }]
              }]"
          :rows="4"
          placeholder=""
        />
      </a-form-item>
      <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 7 }">
        <a-upload-dragger
          :fileList="fileList"
          action="http://10.86.2.51:8080/json/gradeManagement1/upload"
          @change="fileSubmit"
        >
          <p class="ant-upload-drag-icon">
            <a-icon type="inbox"/>
          </p>
          <p class="ant-upload-text">{{this.temMessage}}</p>
        </a-upload-dragger>
      </a-form-item>
      <a-form-item label=" " :colon="false" :label-col="{ span: 6 }" :wrapper-col="{ span: 9 }">
        <a-button type="primary" ghost :style="{marginRight: '10px'}" @click="handleReset"></a-button>
        <a-button type="primary" :style="{marginRight: '20px'}" html-type="submit"></a-button>
        <a-button @click="goBack()"></a-button>
      </a-form-item>
      <br>
      <br>
      <br>
    </a-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      modelform: this.$form.createForm(this),
      visible: false,
      treeData: [],
      load: false,
      fileList: [],
      uploadAddr: undefined,
      temMessage: "",
      tmpItemDetailId: undefined,
      tmpItemGradeId: undefined
    };
  },

  methods: {
    callback(key) {
      console.log(key);
    },

    /**
     * 
     */
    fileSubmit(info) {
      let fileList = [...info.fileList];

      // 1. Limit the number of uploaded files
      // Only to show one recent uploaded files, and old ones will be replaced by the new
      fileList = fileList.slice(-1);

      // 2. read from response and show file link
      fileList = fileList.map(file => {
        if (file.response) {
          // Component will show file.url as link
          file.url = file.response.data;
          /**
           * url
           * 
           * flag
           */
          this.uploadAddr = file.response.data;
          // 
          this.temMessage = file.name;
        }
        return file;
      });
      this.fileList = fileList;
    },

    /**
     * @page: 
     * @limit: 
     */
    fetch() {
      console.log("fetch");
      this.load = true;
      this.axios
        .get("/json/gradeManagement1/courseItemDetail/itemName", {
          params: {
            id: this.$route.query.id
          }
        })
        .then(response => {
          this.load = false;
          let res = response.data;
          console.log(res.data.length);

          // 
          for (let i = 0; i < res.data.length; i++) {
            console.log("this lengthL: " + res.data.length);
            // 
            let tmp = {
              key: i,
              // value: res.data.name + '' + i,
              value: res.data[i].name,
              title: res.data[i].name,
              selectable: false,
              children: []
            };
            // treeData.push();

            for (let j = 0; j < res.data[i].itemNames.length; j++) {
              // 
              tmp.children.push({
                key: i + "-" + j,
                title: res.data[i].itemNames[j].itemName,
                value: res.data[i].itemNames[j].itemName,
                itemDetailId: res.data[i].itemNames[j].itemDetailId,
                itemGradeId: res.data[i].itemNames[j].itemGradeId,
                disabled: false //true
              });
              if (res.data[i].itemNames[j].in === true) {
                // 
                tmp.children[j].disabled = true;
              }
              // console.log(treeData[i].children[j]);
            }
            // console.log(tmp);
            // 
            this.treeData.push(tmp);
          }
        });
    },

    /**
     * 
     * ajax
     */
    handleSubmit(e) {
      e.preventDefault();
      // 
      this.modelform.validateFields((err, values) => {
        console.log(values);
        if (err) {
          // 
          console.log(": ", values);
        } else {
          let load = this.$message.loading("", 0);
          this.axios
            .post("/json/gradeManagement1/stuItemGradeDetail",{
              params: {
                stuId: this.$route.query.stuId,
                courseId: this.$route.query.courseId,
                itemDetail: values.itemDetail,
                content: values.content,
                score: values.score,
                note: values.note,
                uploadAddr: this.uploadAddr,
                itemDetailId: this.tmpItemDetailId,
                itemGradeId: this.tmpItemGradeId
              }
            })
            .then(res => {
              // 
              load();
              if (res.data.code === 0) {
                // 
                this.$message.success("", 4);
                // 
                this.modelform = this.$form.createForm(this);
              } else {
                this.$message.error(res.data.msg, 4);
              }
            });
        }
      });
    },

    /**
     * 
     */
    inputSelect(value) {
      console.log("%%%%%%%%%%%%%%%%");
      // inputitemDetailIditemGradeId
      // let flag = 0;
      for (let i = 0; i < this.treeData.length; i++) {
        for (let j = 0; j < this.treeData[i].children.length; j++) {
          if (value === this.treeData[i].children[j].value) {
            this.tmpItemDetailId = this.treeData[i].children[j].itemDetailId;
            this.tmpItemGradeId = this.treeData[i].children[j].itemGradeId;
            return;
          }
        }
      }
    },

    /**
     *  Select 
     */
    // handleChange(value) {
    //   console.log(`selected ${value}`);
    // },
    // handleBlur() {
    //   console.log("blur");
    // },
    // handleFocus() {
    //   console.log("focus");
    // },
    // filterOption(input, option) {
    //   return (
    //     option.componentOptions.children[0].text
    //       .toLowerCase()
    //       .indexOf(input.toLowerCase()) >= 0
    //   );
    // },

    /**
     * 
     */
    goBack() {
      console.log("this.$route******************");
      console.log(this.$route);
      console.log("************************");
      this.$router.go(-1);
    },

    /**
     * 
     */
    handleReset() {
      this.modelform.resetFields();
    }
  },

  /**
   * ,
   */
  mounted() {
    this.fetch();
  }
};
</script>

<style>
/* .content {
  background: #fff;
  padding: 30px 30px;
} */
</style>


