 * @Description: /
 * @Author: Lin Changkun
 * @LastEditors: Lin Changkun
 * @Date: 2019-05-04 23:15:01
 * @LastEditTime: 2019-06-19 21:50:57
 -->

<template>
  <div class="content">
    <a-form :form="titleform" @submit="handleSearch" layout="inline">
      <!-- <a-form :form="titleform" @submit="handleSearch(1)" layout="inline"> -->
      <a-form-item label="">
        <a-input v-decorator="[
            'courseId'
            ]" placeholder=""/>
      </a-form-item>
      <a-form-item label="">
        <a-input v-decorator="[
            'courseName'
            ]" placeholder=""/>
      </a-form-item>
      <a-form-item label="">
        <a-input v-decorator="[
            'courseClass'
            ]" placeholder=""/>
      </a-form-item>
      <a-form-item>
        <!-- <a-button type="primary" html-type="submit" @click="searchChange"></a-button> -->
        <a-button type="primary" html-type="submit"></a-button>
        <a-button :style="{ marginLeft: '8px' }" @click="handleReset"></a-button>
      </a-form-item>
      <br><br><br>
    </a-form>

    <!--  -->
    <a-table
      :columns="columns"
      :dataSource="data"
      :pagination="pagination"
      :loading="load"
      @change="mainpageChange"
      :row-key="record => record.taskId"
      :scroll="{}"
      class="my-tooltip"
    >
      <!--  -->
      <span slot="tooltip" slot-scope="text">
        <a-tooltip placement="top">
          <template slot="title">{{text}}</template>
          <span>{{text}}</span>
        </a-tooltip>
      </span>

      <!--  -->
      <a-button
        slot="operation"
        slot-scope="text,record"
        type="primary"
        ghost
        @click="showDrawer(record)"
      ></a-button>
    </a-table>

    <!--  -->
    <a-drawer
      title=""
      :width="550"
      @close="onClose"
      :visible="visible"
      :wrapStyle="{height: 'calc(100% - 108px)',overflow: 'auto',paddingBottom: '10 8px'}"
    >
      <a-form :form="modelform" layout="vertical" hideRequiredMark @submit="handleSubmit">
        <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
          <!-- Select / -->
          <a-select
            showSearch
            v-decorator="[
            'name',
             {rules: [{ required: true, message: '' }]
                }]"
            placeholder=""
            optionFilterProp="children"
            style="width: 250px"
            @focus="handleFocus"
            @blur="handleBlur"
            @change="handleChange"
            :filterOption="filterOption"
          >
            <a-select-option value="1"></a-select-option>
            <a-select-option value="2"></a-select-option>
            <a-select-option value="3"></a-select-option>
            <a-select-option value="4"></a-select-option>
            <a-select-option value="5"></a-select-option>
            <a-select-option value="6"></a-select-option>
          </a-select>
        </a-form-item>

        <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
          <a-input-number
            v-decorator="[
            'count',
             {rules: [{ required: true, message: '' }]
                }]"
            style="width: 150px"
            :min="0"
            :step="1"
            :precision="0"
          />
        </a-form-item>

        <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
          <a-input-number
            v-decorator="[
            'rate',
             {rules: [{ required: true, message: '' }]
                }]"
            style="width: 150px"
            :min="0"
            :max="100"
            :formatter="value => `${value}%`"
            :parser="value => value.replace('%', '')"
          />
        </a-form-item>

        <a-button :style="{marginRight: '8px'}" @click="onClose"></a-button>
        <a-button type="primary" html-type="submit"></a-button>
      </a-form>
    </a-drawer>
  </div>
</template>

<script>
// 
// let xx = [];
// for (let i = 0; i < 10; i++) {
//   xx.push({ text: i, value: '"' + i + '"' });
// }

const columns = [
  {
    title: "",
    dataIndex: "semester",
    key: "semester",
    width: 150,
    // specify the condition of filtering result
    // here is that finding the name started with `value`
    onFilter: (value, record) => record.semester.indexOf(value) === 0,
    sorter: (a, b) => a.semester.localeCompare(b) //
  },
  {
    title: "",
    dataIndex: "courseId",
    key: "courseId",
    width: 150,
    // filterMultiple: false,//
    // filters: xx,
    onFilter: (value, record) => record.courseId.indexOf(value) === 0,
    sorter: (a, b) => a.courseId - b.courseId
  },
  {
    title: "",
    dataIndex: "courseName",
    key: "courseName",
    width: 150,
    scopedSlots: { customRender: "tooltip" },
    onFilter: (value, record) => record.courseName.indexOf(value) === 0,
    sorter: (a, b) => a.courseName.localeCompare(b)
  },
  {
    title: "",
    dataIndex: "courseClass",
    key: "courseClass",
    width: 150,
    scopedSlots: { customRender: "tooltip" },
    // filterMultiple: false,
    onFilter: (value, record) => record.courseClass.indexOf(value) === 0,
    sorter: (a, b) => a.courseClass.localeCompare(b)
  },
  {
    title: "",
    dataIndex: "operation",
    width: 150,
    // slots: { name: 'operation'},
    scopedSlots: { customRender: "operation" }
  }
];

const data = [
  {
    key: " ",
    semester: " ",
    courseId: " ",
    courseName: " ",
    courseClass: " ",
    cId: " ",
    operation: " ",
    taskId: " ",
    semesterId: " ",
  }
];

export default {
  data() {
    return {
      modelform: this.$form.createForm(this),
      titleform: this.$form.createForm(this),
      visible: false,
      data,
      columns,
      load: false,
      pagination: {
        pageSize: 5, // 
        current: 1
      },
      tempcId: undefined,
      tempTaskId: undefined,
      tempSemesterId: undefined,

      // tempSearch: false
      // nowTime: new Date() // 
    };
  },

  // watch: {
  //   tempSearch: function (newtempSearch, oldtempSearch){
  //     console.log('------------------');
  //     console.log(this.pagination);
  //     console.log('------------------');
  //     this.pagination.current = 1;

  //   }
  // },

  methods: {
    callback(key) {
      console.log(key);
    },

    /**
     * 
     */
    // mainpageChange(pagination) {
    //   console.log(pagination);
    //   // 
    //   if (this.tempSearch === true) {
    //     // 
    //     this.handleSearch(pagination.current);
    //   } else {
    //     this.fetch(pagination.current, pagination.pageSize);
    //   }
    // },
    mainpageChange(pagination) {
      this.fetch(pagination.current, pagination.pageSize);
    },


    /**
     * @page: 
     * @limit: 
     */
    // fetch(pageNum, limit) {
    //   this.load = true;
    //   this.axios
    //     .get("/json/gradeManagement1/courseItem/listInfo/", {
    //       params: {
    //         pageNum: pageNum
    //       }
    //     })
    //     .then(response => {
    //       this.load = false;
    //       let res = response.data;
    //       console.log(res);
    //       // 
    //       this.data = res.data.list;
    //       // this.formData = res.data.list;
    //       // console.log(formData);
    //       // 
    //       this.pagination = {
    //         total: res.data.total,
    //         pageSize: res.data.pageSize,
    //         current: pageNum,
    //       };
    //     });
    // },

    /**
     * 
     * ajax
     */
    handleSubmit(e) {
      e.preventDefault();
      // 
      this.modelform.validateFields((err, values) => {
        console.log(values);
        if (err) {
          // 
          console.log(": ", values);
        } else {
          let load = this.$message.loading("", 0);
          this.axios
            .post("/json/gradeManagement1/courseItem", {
              courseId: this.tempcId,
              taskId: this.tempTaskId,
              semesterId: this.tempSemesterId,
              name: values.name,
              rate: values.rate,
              count: values.count,

            })
            .then(res => {
              // 
              load();
              if (res.data.code === 0) {
                // 
                this.$message.success("", 4);
                // 
                this.modelform = this.$form.createForm(this);
                this.visible = false;
              } else {
                this.$message.error(res.data.msg, 4);
                this.visible = false;
              }
            });
        }
      });
    },

    /**
     * 
     * 
     * table
     * table
     */
    handleSearch() {
      this.fetch(1, 1);
    },

    // handleSearch(pageNum) {
    fetch(pageNum, limit) {     
      // this.tempSearch = true; // true
      console.log(this.pagination);
      this.titleform.validateFields((err, values) => {
        this.load = true;
        this.axios
          .get("/json/gradeManagement1/courseItem/listInfo", {
            params: {
              pageNum: pageNum,
              // pageNum: this.pagination.current,
              courseId: values.courseId,
              courseName: values.courseName,
              courseClass: values.courseClass
            }
          })
          .then(response => {
            this.load = false;
            let res = response.data;
            console.log(res);
            // 
            this.data = res.data.list;
            // 
            this.pagination = {
              total: res.data.total,
              pageSize: res.data.pageSize
            };
          });
      });
    },

    /**
     * 
     */
    handleReset() {
      this.titleform.resetFields();
    },

    /**
     *  Select 
     */
    handleChange(value) {
      console.log(`selected ${value}`);
    },
    handleBlur() {
      console.log("blur");
    },
    handleFocus() {
      console.log("focus");
    },
    filterOption(input, option) {
      return (
        option.componentOptions.children[0].text
          .toLowerCase()
          .indexOf(input.toLowerCase()) >= 0
      );
    },

    /**
     * 
     */
    showDrawer(record) {
      this.tempTaskId = record.taskId;
      this.tempSemesterId = record.semesterId;
      this.tempcId = record.cId;
      this.visible = true;
    },
    onClose() {
      this.visible = false;
    }
  },

  /**
   * ,
   */
  mounted() {
    this.fetch(1, this.pagination.pageSize);
  }
};
</script>

<style>
/* .content {
  background: #fff;
  padding: 30px 30px;
} */
</style>


