<!--
 * @Description: /
 * @Author: Xiao Xiao
 * @LastEditors: Xiao Xiao
 * @Date: 2019-04-29 15:22:17
 * @LastEditTime: 2019-06-21 15:11:05
 -->

<template>
  <div class="content">
    <div class="safetyInfo">
      <a-row>
      <a-col :span="8">
        {{dataSources1.semesterName }} 
      </a-col>
      <a-col :span="5" :offset="11">
        <a-button type="primary" @click="showSearch"></a-button>
      </a-col>
    </a-row><!--  -->
    </div>


    <!-- / -->
    <a-table 
      :columns="Columns1"
      :dataSource="dataSources1.data"
      :pagination="pagination1"
      :loading="load"
      bordered
      size="small"
      :row-key="record => record.id"
      @change="onChange"
      >
      <a-button slot="studentCode" slot-scope="_,record" style="border-style: none" @click="showclass(record.studentCode)">{{record.studentCode}}</a-button>
      <a-button slot="studentClass" slot-scope="_,record" style="border-style: none" @click="showclass(record.studentCode)">{{record.studentClass}}</a-button>
      <a-button slot="courseNumber" slot-scope="_,record" style="border-style: none" @click="showcourse(record.courseNumber)">{{record.courseNumber}}</a-button>
      <a-button slot="courseName" slot-scope="_,record" style="border-style: none" @click="showcourse(record.courseNumber)">{{record.courseName}}</a-button>
      <a-button slot="operation1" slot-scope="_,record" type="primary" @click="showStudent(record)"></a-button>
    </a-table>
    <!-- 1 -->
    <a-modal title="" :width="550" :visible="visible0" @ok="handleOk0" :confirmLoading="confirmLoading0"
      @cancel="handleCancel0">
      <template>
        <h1>
        {{this.coursedata.number}}<br>
        {{this.coursedata.courseName}}<br>
        {{this.coursedata.courseEname}}<br>
        {{this.coursedata.speciesName}}<br>
        {{this.coursedata.categoryName}}<br>
        {{this.coursedata.classificationName}}<br>
        {{this.coursedata.exam_typeName}}<br>
        {{this.coursedata.exam_modeName}}<br>
        {{this.coursedata.hour}}<br>
        {{this.coursedata.credit}}
        </h1>
      </template>
    </a-modal>
    <!-- 1 -->
    <a-modal title="" :width="550" :visible="visible1" @ok="handleOk1" :confirmLoading="confirmLoading1"
      @cancel="handleCancel1">
        <h1>
        {{this.classdata.departmentName}}<br>
        {{this.classdata.classCode}}<br>
        {{this.classdata.specialtyName}}<br>
        {{this.classdata.className}}<br>
        {{this.classdata.classMonth}}<br>
        {{this.classdata.headteacherName}}<br>
        {{this.classdata.classCover}}<br>
        {{this.classdata.classYear}}<br>
        {{this.classdata.classLength}}
        </h1>
    </a-modal>
    <!-- 1 -->
    <a-modal title="" :width="500" :visible="visible3" @ok="handleOk3" :confirmLoading="confirmLoading3"
      @cancel="handleCancel3">
      <template> 
        <a-form :form="searchForm" @submit="searchSubmit" layout="inline" >
          <a-form-item label="" v-bind="formItemLayout">
            <a-select v-decorator="['StartsemesterName']" placeholder="" optionFilterProp="children" showSearch
              :filterOption="filterSelectedOption" style="width: 200px" :defaultActiveFirstOption="false"> 
              <a-select-option v-for="item0,index in semesterList" :key="index" :value="item0.name">{{item0.name}}</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item label="" v-bind="formItemLayout">
            <a-select v-decorator="['Endsemester']" placeholder="" optionFilterProp="children" showSearch
              :filterOption="filterSelectedOption" style="width: 200px" :defaultActiveFirstOption="false"> 
              <a-select-option v-for="item1,index in semesterList" :key="index" :value="item1.name">{{item1.name}}</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item label="" v-bind="formItemLayout">
            <a-select v-decorator="['courseName']" placeholder="" optionFilterProp="children" showSearch
              :filterOption="filterSelectedOption" style="width: 200px" :defaultActiveFirstOption="false">
              <a-select-option v-for="item2,index in courseList" :key="index" :value="item2.name">{{item2.name}}</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item label="" v-bind="formItemLayout">
            <a-select v-decorator="['speciesName']" placeholder="" optionFilterProp="children" showSearch
              :filterOption="filterSelectedOption" style="width: 200px" :defaultActiveFirstOption="false">
              <a-select-option v-for="item3,index in speciesList" :key="index" :value="item3.name">{{item3.name}}</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item label="" v-bind="formItemLayout">
            <a-select v-decorator="['categoryName']" placeholder="" optionFilterProp="children" showSearch
              :filterOption="filterSelectedOption" style="width: 200px" :defaultActiveFirstOption="false">
              <a-select-option v-for="item4,index in categoryList" :key="index" :value="item4.name">{{item4.name}}</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item label="" v-bind="formItemLayout">
            <a-select v-decorator="['studentClass']" placeholder="" optionFilterProp="children" showSearch
              :filterOption="filterSelectedOption" style="width: 200px" :defaultActiveFirstOption="false">
              <a-select-option v-for="item5,index in ClassList" :key="item5.id" :value="item5.name">{{item5.name}}</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item label="" v-bind="formItemLayout">
            <a-select v-decorator="['studentGrade']" placeholder="" optionFilterProp="children" showSearch
              :filterOption="filterSelectedOption" style="width: 200px" :defaultActiveFirstOption="false">
              <a-select-option v-for="item6,index in GradeList" :key="index"  :value="item6.name">{{item6.name}}</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item label="" v-bind="formItemLayout">
            <a-select v-decorator="['departmentName']" placeholder="" optionFilterProp="children" showSearch
              :filterOption="filterSelectedOption" style="width: 200px" :defaultActiveFirstOption="false">
              <a-select-option v-for="item7,index in departmentList" :key="index" :value="item7.name">{{item7.name}}</a-select-option>
            </a-select>
          </a-form-item>
          <br>
          <a-form-item class="form-btn-item"> <!--  -->
            <a-button class="filter-btn" html-type="submit" type="primary" icon="search"></a-button>
            <a-button class="reset-btn" type="danger" icon="reload" @click="resetSearch"></a-button>
          </a-form-item>
        </a-form>
      </template> 
    </a-modal>

  <br><br><br><br>
  </div>
</template>
<script>
  import Vue from 'vue';
  import { DatePicker} from 'ant-design-vue';
  Vue.use(DatePicker);
  import 'ant-design-vue/dist/antd.css';
  import { Button } from 'ant-design-vue';
  Vue.use(Button);

//1
  const Columns1 = [
    {
      title: '',
      dataIndex: 'studentCode',
      width: 140,
      scopedSlots: {
        customRender: 'studentCode'
      },
    },{
      title: '',
      dataIndex: 'studentClass',
      width: 100,
      scopedSlots: {
        customRender: 'studentClass'
      },
    }, {
      title: '',
      key: 'courseNumber',//ok
      width: 110,
      scopedSlots: {
        customRender: 'courseNumber'
      },
    }, {
      title: '',
      dataIndex: 'courseName',
      scopedSlots: {
        customRender: 'courseName'
      },
    }, {
      title: '',
      dataIndex: 'allNumber',
      width: 100,
    }, {
      title: '',
      dataIndex: 'adoptNumber',//ok
      width: 80,
    }, {
      title: '',
      dataIndex: 'failNumber',
      width: 100,
    }, {
      fixed: 'right',
      title: 'Action',
      key: 'operation1',//ok
      width: 120,
      scopedSlots: {
      customRender: 'operation1'
      },
    }
  ];

  export default {
    data() {

      return {
//
        Columns1,//
        load:false,//
        search: '',
        searchData: '',
        searchForm:this.$form.createForm(this),
        pagination1:{total:0,pageSize:5},//
        semesterList: [
          {id:1,name:''},
        ],//
        departmentList: [
          {id:1,name:''},
        ],//
        GradeList: [
          {id:1,name:'2014'},
          {id:2,name:'2015'},
          {id:3,name:'2016'},
          {id:4,name:'2017'},
          {id:5,name:'2018'},
          {id:6,name:'2019'},
          {id:7,name:'2020'},
        ],//
        ClassList: [
          {id:1,name:''},
        ],//
        courseList: [
          {id:1,name:''},
        ],//
        speciesList: [
          {id:1,name:''},
        ],//
        categoryList: [
          {id:1,name:''},
        ],//
        dataSources1:{ 
          total:13,
          data:[
          ]
        },
        visible3: false, //modal
        confirmLoading3: false, //modal
//
        classdata:{},//
        coursedata:{},//
        visible0: false, //modal
        confirmLoading0: false, //modal
        visible1: false, //modal
        confirmLoading1: false, //modal
        formItemLayout: {
          labelCol: { span: 8 },
          wrapperCol: { span: 14 },
        },

        a:1,
      }
    },
      created: function () {
        this.axios
          .get("/json/gradeManagement1/selectByTeacher",)
          .then(response => {
            // this.load=false,
            this.dataSources1=response.data.data;
            this.pagination1={total:response.data.data.total,pageSize:5}
            console.log(response.data.data);
          })
          .catch(function (error) {
            console.log(error);
        });//get
        this.axios
          .get("/json/educateAffair/semester/listAll",)
          .then(response => {
              this.semesterList=response.data.semesters;
            console.log("this.semesterList");
            console.log(response.data);
          })
          .catch(function (error) {
            console.log(error);
        });//
        this.axios
          .get("/json/educateAffair/curriculum/getInstitute",)
          .then(response => {
              this.departmentList=response.data.Institute;
            console.log("this.departmentList");
            console.log(response.data);
          })
          .catch(function (error) {
            console.log(error);
        });//
        this.axios
          .get("json/administrativestructure/classes/listAll",)
          .then(response => {
              this.ClassList=response.data.classes;
            console.log("this.ClassList");
            console.log(response.data);
          })
          .catch(function (error) {
            console.log(error);
        });//
        this.axios
          .get("json/professionalCourses/courses/listAll",)
          .then(response => {
              this.courseList=response.data.courses;
            console.log("this.courseList");
            console.log(response.data);
          })
          .catch(function (error) {
            console.log(error);
        });//
        this.axios
          .get("json/professionalCourses/courseSpeciess/listAll",)
          .then(response => {
              this.speciesList=response.data.courseSpeciess;
            console.log("this.speciesList");
            console.log(response.data);
          })
          .catch(function (error) {
            console.log(error);
        });//
        this.axios
          .get("json/professionalCourses/courseCategorys/list/1")
          .then(response => {
              this.categoryList=response.data.pageInfo.list;
            console.log("this.categoryList");
            console.log(response.data);
          })
          .catch(function (error) {
            console.log(error);
        });//
      },//

    methods: {
//
      filterSelectedOption(input, option) { //
        return option.componentOptions.children[0].text.toLowerCase().indexOf(input.toLowerCase()) >= 0
      },
      btn: function () {
        var search = this.search;
        if (search) {
          this.searchData = this.products.filter(function (product) {
            console.log(product)
            return Object.keys(product).some(function (key) {
              console.log(key)
              return String(product[key]).toLowerCase().indexOf(search) > -1
            })
          })
        }
      },//
      showSearch(){
        this.visible3=true;
      },//
      searchSubmit (e) {
        e.preventDefault();
        this.searchForm.validateFields((err, values) => {
          if (!err) {
            console.log('Received values of form: ', values);
            this.axios
              .get("/json/gradeManagement1/selectAllByexample",{//GET /json/gradeManagement1/selectAllByexample
                  params:{
                    semesterStart:values.semesterStart,
                    semesterEnd:values.semesterEnd,
                    courseName:values.courseName,
                    speciesName:values.speciesName,
                    categoryName:values.categoryName,
                    studentClass:values.studentClass,
                    studentGrade:values.studentGrade,
                    departmentName:	values.departmentName
                  }
                }
              )
              .then(response => {
                // this.load=false,
                this.dataSources1=response.data.data;
                this.pagination1={total:response.data.data.total,pageSize:5}
                console.log("");
                console.log(this.dataSources1);
                console.log("");
                 this.visible3 = false;
              })
              .catch(function (error) {
                console.log(error);
            });
          }
        });
        this.searchForm.resetFields()
      },//
      resetSearch(){
        this.searchForm.resetFields()
      },//
      handleOk3(e) {
        this.confirmLoading3 = true;
        setTimeout(() => { this.visible3 = false; this.confirmLoading3 = false; }, 500);
        this.searchForm.resetFields()
      }, //model3
      handleCancel3(e) {
        this.visible3 = false;
        this.searchForm.resetFields()
      }, //model3

//   
      showcourse(value){
        this.axios
        .get("/json/gradeManagement1/selectBycourseNumber",
            {
              params: {
                courseNumber:value
              }
            }
          )
        .then(response=>{
          this.coursedata=response.data.data;
          console.log(this.coursedata);
        })
        .catch(function (error) {
            console.log(error);
        })
        this.visible0=true
      },//
      handleOk0(e) {
        this.confirmLoading0 = true;
        setTimeout(() => { this.visible0 = false; this.confirmLoading0 = false; }, 500);
      },//model0
      handleCancel0(e) {
        this.visible0 = false;
      },//model
      showclass(value){
        this.axios
        .get("/json/gradeManagement1/selectByclassCode",//
            {
              params: {
                classCode:value
              }
            }
          )
        .then(response=>{
          this.classdata=response.data.data;
          if(this.classdata.classLength==1)
            this.classdata.classLength=""
          else if(this.classdata.classLength==6)
            this.classdata.classLength=""
          else if(this.classdata.classLength==8)
            this.classdata.classLength=""
          if(this.classdata.classCover==false)
            this.classdata.classCover=""
          else if(this.classdata.classCover==true)
            this.classdata.classCover=""
          console.log(this.classdata);

        })
        .catch(function (error) {
            console.log(error);
        })
        this.visible1=true
      },//
      handleOk1(e) {
        this.confirmLoading1 = true;
        setTimeout(() => { this.visible1 = false; this.confirmLoading1 = false; }, 500);
      },//model0
      handleCancel1(e) {
        this.visible1 = false;
      },//model
      
      showStudent(value) {
        const num=value.id-1;
        this.dataSources2=value,
        this.pagination2={total:value.allNumber,pageSize:this.dataSources1.pageSize}
        this.$router.push({
          path:"./officerSearchGrade",
          query:{
            data1:this.dataSources2
          }
        });
        console.log(this.dataSources2)
        //this.visible = true
      }, // 

//
      fetch(current,pageSize){
      },
      onChange(pagination, sorter) {
        this.fetch(pagination.current,5);
      },//fetch

    }
  }
</script>

<style>
.ant-table td { white-space: nowrap; }
</style>



