 -->

<template>
<div class="content">

  <div>
    <a-row>
      <a-col :span="10">
        {{dataSources2.courseName}}
      </a-col>
      <a-col :span="8">
        {{dataSources2.courseNumber}}
      </a-col>
    </a-row>
    <br>
    <a-row>
      <a-col :span="10">
        {{dataSources2.studentClass}}
      </a-col>
      <a-col :span="8">
        {{dataSources2.studentCode}}
      </a-col>
    </a-row>
  </div>
    <a-row type="flex" justify="end">
      <a-col :span="3">
      <a-button type="primary" @click="goBack()"></a-button>
      </a-col>
    </a-row>

    <!-- 1 -->
    <a-modal title="" :width="650" :visible="visible0" @ok="handleOk0" :confirmLoading="confirmLoading0"
      @cancel="handleCancel0">
      <template>
        {{this.studentdata.specialtyName}}<br>
        {{this.studentdata.address}}<br>
        {{this.studentdata.studentName}}<br>
            {{this.studentdata.grade}}<br>
        {{this.studentdata.begin_learn_date}}<br>
        {{this.studentdata.political}}<br>
        {{this.studentdata.className}}<br>
        {{this.studentdata.communication}}<br>
            {{this.studentdata.stuNo}}<br>
      </template>
    </a-modal>

    <!--  -->
    <template>
      <a-table 
          :columns="Columns2"
          :dataSource="dataSources2.stuItemGradeVOList"
          :pagination="pagination2"
          :loading="load"
          bordered
          size="small"
          :row-key="record => record.id"
          @change="onChange"
        >
        <a-button slot="stuNo" slot-scope="_,record" style="border-style: none" @click="showstudent(record.studentId)">{{record.stuNo}}</a-button>
        <a-button slot="studentName" slot-scope="_,record" style="border-style: none" @click="showstudent(record.studentId)">{{record.studentName}}</a-button>
        <a-button slot="operation1" slot-scope="_,record" type="primary" @click="showDetailed(record)"></a-button>
      </a-table>
    </template>

</div>
</template>

<script>

  // _1 
  const Columns2 = [
    {
      title: '',
      width: 130,
      key: 'stuNo',
      scopedSlots: {
      customRender: 'stuNo'
      },
      sorter: (a, b) => a.stuNo - b.stuNo,
    }, {
      title: '',
      key: 'studentName',
      scopedSlots: {
      customRender: 'studentName'
      },
      //width: 80,
    }, {
      title: '',
      dataIndex: 'speciesName',//1111111111111
      width: 110,
    }, {
      title: '',
      dataIndex: 'categoryName',//1111111111111  categoryName
      width: 110,
    }, {
      title: '',
      dataIndex: 'score',//
      width: 90,
      sorter: (a, b) => a.score - b.score,
    }, {
      title: '',
      dataIndex: 'point',//ok
      width: 70,
      sorter: (a, b) => a.point - b.point,
    }, {
      title: '',
      dataIndex: 'state',//ok
      width: 80,
    }, {
      title: '',
      dataIndex: 'teacherName',
      width: 100,
    }, {
      fixed: 'right',
      title: '',
      key: 'operation1',//ok
      width: 90,
      scopedSlots: {
      customRender: 'operation1'
      },
    }
  ];

  const formItemLayout = {
    labelCol: {
      span: 6
    },
    wrapperCol: {
      span: 12
    },
  };

export default {
  data(){
    return{
      Columns2,
      load:false,
      form:this.$form.createForm(this),////dataSources2:this.$route.query.data1
      dataSources2:this.$route.query.data1,
      studentdata:{},//
      visible0: false, //modal
      confirmLoading0: false, //modal
      pagination2:{total:2,pageSize:5},
  //
      Detaileddata:[
        {id:1}
      ],//&_1
      data: [
          {
          score: undefined,
          rate: undefined,
          name: '',
          count: undefined,
          id: 1
          },{
          score: undefined,
          rate: undefined,
          name: '',
          count: undefined,
          id: 2
          },{
          score: undefined,
          rate: undefined,
          name: '',
          count: '',
          id: 3
          },{
          score: undefined,
          rate: undefined,
          name: '',
          count: undefined,
          id: 4
          },{
          score: undefined,
          rate: undefined,
          name: '',
          count: undefined,
          id: 5
          },{
          score: undefined,
          rate: undefined,
          name: '',
          count: undefined,
          id: 6
          },
      ], //_1
      inputId:{
        courseId:'',
        stuGradeMainId:'',
        studentId:'',
        universityId:'',
        semesterId:'',
        stuItemGradeDetailOldId:''
      },

      data0:"2",
    }

  },
    mounted () {
    this.form = this.$form.createForm(this);
    this.pagination2={total:this.dataSources2.allNumber,pageSize:5}
    },
  methods: {

    
      showstudent(value){
        this.axios
        .get("/json/gradeManagement1/selectBystudentinform",
            {
              params: {
                studentId:value
              }
            }
          )
        .then(response=>{
          this.studentdata=response.data.data;
          console.log(value);
          console.log(this.studentdata);
        })
        .catch(function (error) {
            console.log(error);
        })
        this.visible0=true
      },//
      handleOk0(e) {
        this.confirmLoading0 = true;
        setTimeout(() => { this.visible0 = false; this.confirmLoading0 = false; }, 500);
      }, //model0
      handleCancel0(e) {
        this.visible0 = false;
      }, //model

    //
      showDetailed(value) { //
        this.axios
          .get("/json/gradeManagement1/selectCourseItem", {
            params: {
              id: value.id,
            }
          })
          .then(response => {
             for(let i=0;i<6;i++){
               if( response.data.data[i]!=undefined)
                 {
                    this.data[i] = response.data.data[i];
                    if(this.data[i].name==1)
                    this.data[i].name = '';
                    else if(this.data[i].name==2)
                    this.data[i].name = '';
                    else if(this.data[i].name==3)
                    this.data[i].name = '';
                    else if(this.data[i].name==4)
                    this.data[i].name = '';
                    else if(this.data[i].name==5)
                    this.data[i].name = '';
                    else if(this.data[i].name==6)
                    this.data[i].name = '';
                 }
             };
            this.Detaileddata=this.data;
            this.inputId.courseId=value.courseId
            this.inputId.studentId=value.studentId
            this.inputId.universityId=value.universityId
            this.inputId.semesterId=value.semesterId
            this.inputId.stuGradeMainId=value.id
            console.log(this.inputId);
            console.log('this.value  A');
            this.$router.push({
              path:"./DetailedSearchGrade",
              query:{
                data0:this.data0,
                data1:this.Detaileddata,
                data2:this.dataSources2,
                data3:this.inputId,
              }
            });
          })
          .catch(function (error) {
            console.log(error);
          });
        //this.visible1 = true
      }, //model1 

    //
      onChange(pagination, sorter) {
        this.fetch(pagination.current,1);
      },
      //
      fetch(current,pageSize){
        this.pagination1={total:this.dataSources2.allNumber,pageSize:5}
      },

    //
      goBack() {
        this.$router.push({
          path:"./teacherSearchGrade",
          query:{
            ok:"ok"
          }
        });
      },
  }
}
</script>

<style>

</style>


