<template>
  <div class="content">

    <div>
    <a-row type="flex" justify="end">
      <a-col :span="3">
        <a-button type="primary" @click="goBack()"></a-button>
      </a-col>
    </a-row>
    </div>
    
    <!-- 1 -->
      <template>
        <!-- _1 -->
        <a-table :columns="Columns" :dataSource="Detaileddata" :pagination="false" size="small"
          :rowKey="record => record.course_Item_Id" @expand="expand" :expandedRowKeys="expandedRowKeys"
          class="components-table-demo-nested">
          <!-- _2 -->
          <!-- data.list. -->
          <a-table slot="expandedRowRender" :pagination="false" :columns="innerColumns" :dataSource="innerData"
            size="small" :row-key="record => record.number">
            <a slot="operation2" slot-scope="record" :href="'json/gradeManagement1/download?path=' + record.attachment">
              <a-button icon="download"></a-button>
            </a>
            <a-button slot="operation3" slot-scope="record" @click="showApply(record)"></a-button>
          </a-table>
          <!-- _2 -->
        </a-table>
        <!-- _1 -->
      </template>

  </div>
</template>

<script>
  //_1
  const Columns = [{
    title: '',
    dataIndex: 'name',
    width: 490,
    key: 'name'
  }, {
    title: '',
    dataIndex: 'rate',
    width: 200,
    key: 'rate'
  }, {
    title: '',
    dataIndex: 'count',
    width: 120,
    key: 'count'
  }, {
    title: '',
    dataIndex: 'score',
    key: 'score'
  }]; /* { title: 'Action', key: 'operation', scopedSlots: { customRender: 'operation' } }*/
  // _2
  const innerColumns = [{
      title: '',
      dataIndex: 'number',
      key: 'number'
    },
    {
      title: '',
      dataIndex: 'content',
      key: 'content'
    },
    {
      title: '',
      key: 'attachment',
      scopedSlots: {
        customRender: 'operation2'
      }
    },
    {
      title: '',
      dataIndex: 'score',
      key: 'score'
    },
    {
      title: '',
      key: 'operation3',
      scopedSlots: {
        customRender: 'operation3'
      }
    },
  ];

export default {
    data() {
      return {
        //
        data0: this.$route.query.data0,
        //
        Detaileddata: this.$route.query.data1, //&_1
        dataSources2:this.$route.query.data2,//dataSources2
        inputId:this.$route.query.data3,//inputid
        Columns,
        data: [
            {
            score: undefined,
            rate: undefined,
            name: '',
            count: undefined,
            id: 1
            }, {
            score: undefined,
            rate: undefined,
            name: '',
            count: undefined,
            id: 2
            }, {
            score: undefined,
            rate: undefined,
            name: '',
            count: '',
            id: 3
            }, {
            score: undefined,
            rate: undefined,
            name: '',
            count: undefined,
            id: 4
            }, {
            score: undefined,
            rate: undefined,
            name: '',
            count: undefined,
            id: 5
            }, {
            score: undefined,
            rate: undefined,
            name: '',
            count: undefined,
            id: 6
            }, 
        ], //_1
        expandedRowKeys: [], //key
        innerColumns, // _2
        innerData: [{
          number: '1',
          score: '',
          attachment: '',
          content: ''
        }], // _2
        visible1: false, //modal
        confirmLoading1: false, //modal
      }

    },
    methods: {

      //

      expand(expanded, record) { //
        this.innerData = []
        console.log('---------------')
        if (record.name == "") {
          this.axios.get("/json/gradeManagement1/selectCourseItemDetail1", {
              params: {
                course_item_id: record.course_Item_Id,
                stu_item_grade_id: record.stu_item_grade_Id
              }
            })
            .then(response => {
              this.innerData = response.data.data;
            })
            .catch(function (error) {
              console.log(error);
            });
        }
        if (record.name == "") {
          this.axios.get("/json/gradeManagement1/selectCourseItemDetail2", {
              params: {
                course_item_id: record.course_Item_Id,
                stu_item_grade_id: record.stu_item_grade_Id
              }
            })
            .then(response => {
              this.innerData = response.data.data;
            })
            .catch(function (error) {
              console.log(error);
            });
        }
        if (record.name == "") {
          this.axios.get("/json/gradeManagement1/selectCourseItemDetail3", {
              params: {
                course_item_id: record.course_Item_Id,
                stu_item_grade_id: record.stu_item_grade_Id
              }
            })
            .then(response => {
              this.innerData = response.data.data;
            })
            .catch(function (error) {
              console.log(error);
            });
        }
        if (record.name == "") {
          this.axios.get("/json/gradeManagement1/selectCourseItemDetail4", {
              params: {
                course_item_id: record.course_Item_Id,
                stu_item_grade_id: record.stu_item_grade_Id
              }
            })
            .then(response => {
              this.innerData = response.data.data;
            })
            .catch(function (error) {
              console.log(error);
            });
        }
        if (record.name == "") {
          this.axios.get("/json/gradeManagement1/selectCourseItemDetail5", {
              params: {
                course_item_id: record.course_Item_Id,
                stu_item_grade_id: record.stu_item_grade_Id
              }
            })
            .then(response => {
              this.innerData = response.data.data;
            })
            .catch(function (error) {
              console.log(error);
            });
        }
        if (record.name == "") {
          this.axios.get("/json/gradeManagement1/selectCourseItemDetail6", {
              params: {
                course_item_id: record.course_Item_Id,
                stu_item_grade_id: record.stu_item_grade_Id,
              }
            })
            .then(response => {
                this.innerData = response.data.data;
            })
            .catch(function (error) {
              console.log(error);
            });
        }
        if (this.expandedRowKeys[0] != record.course_Item_Id || this.expandedRowKeys.length == 0)
          this.expandedRowKeys = [record.course_Item_Id]
        else
          this.expandedRowKeys = []
        setTimeout(() => {
          console.log('---------------')
        }, 1500);
      },

      //
      showApply(value) {
        //this.visible2 = true;
        console.log(value)
        this.inputId.stuItemGradeDetailOldId=value.stu_item_grade_detail_id
        this.$router.push({
            path:"./ApplySearchGrade",
            query:{
                data0:this.data0,
                data1:this.Detaileddata,
                data2:this.dataSources2,
                data3:this.inputId,
          }
        });
      }, //model2 
      handleOk2(e) {
        this.confirmLoading2 = true;
        setTimeout(() => {
          this.visible2 = false;
          this.confirmLoading2 = false;
        }, 500);
        this.inputForm.resetFields()
      }, //model2
      handleCancel2(e) {
        this.visible2 = false;
        this.inputForm.resetFields()
      }, //model2model1
      
      //
    goBack() {
        if(this.data0=="1"){
            this.$router.push({
              path:"./studentSearchGrade",
              query:{
                data1:this.dataSources2,
              }
            });
        }
        else if(this.data0=="2"){
            this.$router.push({
              path:"./officerSearchGrade",
              query:{
                data1:this.dataSources2,
              }
            });
        }
    },
    }
}
</script>

<style>

</style>


